
.form{
    margin-left: auto;
    display: flex;
    align-items: center;
    
  
}

.items{
    display: block;
    width: 200px;
   
}
.button{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 120px;
    height: 40px;
    
    border-radius: 10px;
    background-color: rgba(210, 215, 144, 0.227);
    box-shadow: 1px 2px 27px 1px rgba(0,0,0,0.67);
    border: 0;
    font-size: 14px;
}
.button:active{    
    background-color: beige;
    box-shadow: 1px 2px 10px 1px rgba(0,0,0,0.67);}

.logo{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 250px;
    height: 250px;
    margin-left: 50px;
    border-radius: 50%;
    background-color: rgba(0, 0, 255, 0.227);
}
