.form{
    box-sizing: border-box;
    width: 300px;
    padding: 20px;
    margin-bottom: 20px;
    
    box-shadow: 0px 0px 16px 8px #56555566;

    background-color: rgb(239, 239, 239);
    border-radius: 10px;

}
.text{
    font-weight: 700;
}
.input{
    width: 100%;
    height: 30px;
}

.button{
    width: 180px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
    padding: 10px;
    background-color: rgba(165, 165, 204, 0.907);
    border-radius: 5px;
    font-weight: 700;
    border: 0;
    box-shadow: 0px 0px 16px 4px rgba(136, 134, 134, 0.4);
    
}
.button span{
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.button:active{
    background-color: rgb(165, 165, 248);
    box-shadow: 0px 0px 16px 8px rgba(128, 122, 122, 0.4);

}
